<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Some Links</h2>
    <ul>
      <li>
        <a href="https://chinecc.github.io/alibazarbms/" target="_blank">
          Alibazar BMS
        </a>
      </li>
      <li>
        <a href="https://chinecc.github.io/wdclab/" target="_blank">
          Employee Manager
        </a>
      </li>

    </ul>

  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: "Welcome to chinecc's pages"
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 20px 10px;
  }

  a {
    color: #42b983;
  }

</style>



